$med-gray: #666666;
$black: #252525; 
$white: #ffffff;
$highlight: #0fbfc5;

$font: 'Roboto', sans-serif;
$fontW-light: 300;
$fontW-reg: 400;
$fontW-bold: 500;
$fontS-reg: 13px;

$text-caps: uppercase;

$media: screen;
$media-min: min-width; 
$width-med: 35em;
$width-large: 46em;

$mpadding: 10px 20px 10px 40px;
$dpadding: 0 20px;

@mixin bodyBase(){
	font-family: $font;
	font-weight: $fontW-reg;
	color: $med-gray;
	font-size: $fontS-reg;

}

@mixin paraText(){
	font-weight: $fontW-light;
	font-size: 22px;
}

@mixin button(){
	background-color: $highlight;
	color: $white;
	text-transform: $text-caps;
}

body {
	@include bodyBase;
}
a{
	text-decoration: none;
}

img {
	width: 100%;
}

.hide {
	display: none;
}

header{

	.header-content {
		background-color: $black;
		color: $white;
		height: 20px;
		padding: 20px;
	}
	a {
		color: $white;
	}
	#header-logo {
		width: 112px;
	}
}
.content {
	text-align: center;
	margin: 0 auto;
}
h1 {
	font-weight: 300;
	}

	.page {
		width: 320px; // MOBILE
		text-align: center;
		margin: 0 auto;
	}

.country {
	border-bottom: 1px solid #ccc;

}
.state-block{	  
	padding: 20px 0;
	text-align: left;

	.state-main {
		height: 100%;
    	display: flex;
    	align-items: center;
	}

	.thumb {
		width: 54px;
	}
	.text{
		padding-left: 20px;
		.name {
			display: inline-block;
			font-weight: $fontW-bold;
			text-transform: uppercase;
			font-size: 17px;
		}
		.port-no{
			display: inline-block;
		}
	}
	.ports {
		position: relative;
    	left: 64px;
		.port-name{
			text-transform: uppercase;
			text-transform: uppercase;
		    padding: 12px 10px 11px 10px;
		    border: 1px solid #ccc;
		    margin-top: -1px;
		} 
	}
}
